import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
export default {
	components: {
		empty,
		uniLoadMore
	},
	data() {
		return {
			shopsList: [],
			pageIndex: 1,
			limitNum: 15,
			searchKey: '',
			hasKey: false,
			hasShops: false,
			loadingType: 'more',
		};
	},
	onLoad(options) {
		// 判断是否有关键字
		if (Object.keys(options).length !== 0) {
			console.log('有搜索关键字')
			console.log(options)
			this.hasKey = true
			this.searchKey = options.searchKey
			this.getShops()
		} else {
			console.log('没有搜索关键字')
			this.hasKey = false
		}
		
	},
	onReachBottom() {
		if (this.loadingType === 'noMore') {
			return
		} else {
			this.getShops()
		}
	},
	methods: {
		// 去搜索页
		toSearch() {
			uni.navigateTo({
				url: '/pages/index/search?keyWords=' + this.searchKey
			})
		},
		// 去商品详情页
		toShopDetail() {
			uni.navigateTo({
				url: '/pages/index/shopDetail/shopDetail'
			})
		},
		// 搜索商品
		getShops() {
			if (this.loadingType === 'loading') {
				//防止重复加载
				return;
			}
			
			this.loadingType = 'loading';

			// 请求数据
			setTimeout(() => {
				this.$api.search({
					keywords: this.searchKey,
					limit: this.limitNum,
					page: this.pageIndex
				}).then((res) => {
					// console.log(res)
					if (res.data.errno === 0) {
						console.log(this.shopsList)
						if(res.data.data.goods.length !== 0){
							// console.log('有数据')
							this.hasShops = true
							res.data.data.goods.map(item => {
								console.log(item)
								this.shopsList.push(item)
							})
							//判断是否还有数据()通过判断是否还有下一页， 有改为 more， 没有改为noMore
							if(res.data.data.next_page === 0){
								this.loadingType = 'noMore'
							}else{
								this.loadingType = 'more'
								this.pageIndex = res.data.data.next_page
							}
						}else {
							// console.log('没有数据')
							this.hasShops = false
						}
					}else{
						this.loadingType = 'noMore'
						console.log(res.data.msg)
					}
				})

			}, 600)

		}
	}
}
